<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <inputCar v-if="currentStep === steps.findIndex(step=>step.key === 'inputCar')" :nextFunction="inputCarInfo" :lastInput="lastInput"/>
        <text-appear v-if="currentStep === steps.findIndex(step=>step.key === 'complete')" :isComplete="true" :formListData="driverUserInfo"/>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import inputCar from '@/view/pageComponents/input-car';
import textAppear from '@/view/pageComponents/text-appear';
import { getVehicleQueryCarInfo } from '@/service/traffic/traffic';
import { DRIVER_CAR_CHECK } from '@/constant/stepsOption';
import { LOCATION_ID } from '@/constant/constant';

export default {
    name: 'VehicleInfoQueryIndex',
    components: {
        textAppear,
        identifyCardPage,
        faceYitu,
        contrast,
        inputCar
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep'
        })
    },
    data () {
        return {
            driverUserInfo: {},
            lastInput: false
        };
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        preStep () { // 上一步
            this.changeStep('up');
        },
        async inputCarInfo ({ carLicense, carType, clsbdm }) {
            const res = await getVehicleQueryCarInfo({
                plateType: carType,
                plateNumber: carLicense,
                clsbdm: clsbdm,
                type: '1'
            });
            if (res && res.code === '0') {
                this.driverUserInfo = res.result;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, imgName: 'imgCar', enableBack: true });
            }
        }
    },
    mounted () {
        if (this.CONSTANT.location === LOCATION_ID.yinchuan || this.CONSTANT.location === LOCATION_ID.zhongwei) {
            this.lastInput = true;
        }
        this.setSteps(DRIVER_CAR_CHECK);
    }
};

</script>
